      <p>Auch während Sie unsere Website jetzt gerade besuchen, speichert unser Webserver &#8211; das ist der Computer auf dem diese Webseite gespeichert ist &#8211;  aus Gründen der Betriebssicherheit, zur Erstellung von Zugriffsstatistik usw. in der Regel automatisch Daten wie</p>
