      <li class="adsimple-311264162">Name: _ga</li>
      <li class="adsimple-311264162">Ablaufzeit: 2 Jahre</li>
      <li class="adsimple-311264162">Verwendung: Unterscheidung der Webseitenbesucher</li>
      <li class="adsimple-311264162">Beispielhafter Wert: GA1.2.1326744211.152311264162</li>
      </ul>
      <p>Ein Browser sollte folgende Mindestgrößen unterstützen:</p>
      <ul class="adsimple-311264162">
