        <p>Staatliche Zuschüsse erhalten Unternehmen zum Beispiel für Forschungen, Entwicklungen und Innovationen. Im Bereich Unternehmensfinanzierungen, im Zuge der Gründungs- und Mittelstandsförderung, werden auch branchenunabhängige Digitalisierungsmaßnahmen regelmäßig bezuschusst. In einem solchen Fall sind kleine und mittlere Unternehmen, aber auch Freiberufler antragsberechtigt. Ein weiteres Antragskriterium ist mitunter der Sitz des Unternehmens. So kann sich etwa das Fördergebiet auf Hessen beschränken, wenn der Fördergeber das Land Hessen selbst ist.</p>

        <p>Mit dem oben genannten Beispiel der Bezuschussung von Digitalisierungsmaßnahmen sollen u.a. Produktions- und Arbeitsprozesse digitalisiert werden, um dadurch Wachstum und Effizienzvorteile für kleinere und mittlere Unternehmen ermöglichen zu können. Solche kleineren Zuschüsse können auf eine bestimmte Förderhöchstsumme begrenzt sein, beispielsweise 5.000 oder 10.000 Euro, und müssen im Falle der vorgegebenen korrekten Verwendung nicht zurückgezahlt werden.</p>
